import React from "react";
import "./style/perf.scss";
import firma from "../img/firma.png";
import Cronometro from "./Cronometro";
import Hora from './Hora';

export default class Performace extends React.Component {

  constructor(props){
    super(props);
    this.state = {
      crono: 'stop'
    }
  }

  handleCronoStart = () => {
    console.log("si");
    if (this.state.crono === "stop" || this.state.crono === "restart") {
      this.setState({ crono: "play" });
    }
  };

  handleCronoStop = () => {
    if (this.state.crono === "play" || this.state.crono === "restart") {
      this.setState({ crono: "stop" });
    }
  };

  handleCronoRestart = () => {
    if (this.state.crono === "stop" || this.state.crono === "play") {
      this.setState({ crono: "restart" });
    }
  };

  handleShanger = ()=>{
    this.setState({
      crono: 'stop'
    })
  }
  
  //`${this.state.hora}:${this.state.minuto}.${this.state.segundo}`
  render() {
    let glass;
    if (this.props.clockState === "hora") {
      glass = <Hora onShanger={this.handleShanger}/>;
    } else if (this.props.clockState === "cronometro") {
      glass = <Cronometro  state={this.state.crono} />;
    }
    return (
      <React.Fragment>
        <div className="container-perf">
          <div className="options-perf">
            <input
              onClick={this.handleCronoStart}
              className="button-perf w3-highway-green"
              type="button"
              value="START"
            />
            <input
              onClick={this.handleCronoStop}
              className="button-perf w3-highway-red"
              type="button"
              value="STOP"
            />
            <input
              onClick={this.handleCronoRestart}
              className="button-perf w3-highway-orange"
              type="button"
              value="RESTART"
            />
            <img src={firma} alt="" />
          </div>
          <div className="screen-perf">
            <div className="screen-origin">
              <h1 className={this.props.power}>{glass}</h1>
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
}
