import React from "react";
import "./style/clock.scss";
import Screen from "./Screen";
import Performace from "./Performace";

export default class Clock extends React.Component {
  state = {
    activo: 'stop',
    clockState: "hora",
    power: "init"
  };
  handleOnClick = () => {
    if (this.state.power === "init" || this.state.power === "off") {
      this.setState({ power: "on" });
    }
  };
  handleOffClick = () => {
    if (this.state.power === "on") {
      this.setState({ power: "off" });
    }
  };
  handleHoraClick = () => {
    if (this.state.power === "on") {
      this.setState({ 
        clockState: "hora"
       });
    }
  };
  handleCronoClick = () => {
    if (this.state.power === "on") {
      this.setState({ clockState: "cronometro" });
    }
  };
  render() {
    return (
      <React.Fragment>
        <div className="container">
          <div className="container-clock-b">
            <div className="header-clock">
              <div
                onClick={this.handleHoraClick}
                className="pup w3-highway-green"
              >
                <h2>H</h2>
              </div>
              <div
                onClick={this.handleCronoClick}
                className="pup w3-highway-yellow"
              >
                <h2>C</h2>
              </div>
              <div onClick={this.handleOnClick} className="pup w3-highway-red">
                On
              </div>
              <div
                onClick={this.handleOffClick}
                className="pup w3-highway-orange"
              >
                Off
              </div>
              <div className="screen">
                <Screen
                  clockState={this.state.clockState}
                  power={this.state.power}
                />
              </div>
              <div className="Augus">Augus</div>
            </div>
            <Performace activo={this.state.activo} clockState={this.state.clockState} power={this.state.power}/>
          </div>
        </div>
      </React.Fragment>
    );
  }

  newMethod() {
    return <h1>hora</h1>;
  }
}
