import React from "react";

class Cronometro extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      activo: "si",
      s: "00",
      m: "00",
      h: "00"
    };
  }
  componentDidMount() {
    this.timerID = setInterval(() => {
      this.tick();
    }, 1000);
  }
  componentWillUnmount() {
    clearInterval(this.timerID);
  }

  tick = () => {
    let sec = parseInt(this.state.s);
    let min = parseInt(this.state.m);
    let hor = parseInt(this.state.h);
    if (this.props.state === "play" && this.state.activo === "si") {
      if (sec < 9) {
        this.setState({ s: `0${sec + 1}` });
      }
      if (sec >= 9 && sec < 59) {
        this.setState({ s: `${sec + 1}` });
      }
      if (sec === 59 && min < 9) {
        this.setState({
          s: "00",
          m: `0${min + 1}`
        });
      }
      if (sec === 59 && min >= 9 && min < 59) {
        this.setState({
          s: "00",
          m: `${min + 1}`
        });
      }
      if (sec === 59 && min === 59 && hor < 9) {
        this.setState({
          s: "00",
          m: "00",
          h: `0${hor + 1}`
        });
      }
      if (sec === 59 && min === 59 && hor >= 9 && hor < 99) {
        this.setState({
          s: "00",
          m: "00",
          h: `${hor + 1}`
        });
      }
      if (hor === 99 && min === 59 && sec === 59) {
        this.setState({
          activo: "no"
        });
      }
    }
    if (this.props.state === "restart") {
      this.setState({
        s: "00",
        m: "00",
        h: "00"
      });
    }
  };

  render() {
    return `${this.state.h}:${this.state.m}.${this.state.s}`;
  }
}

export default Cronometro;
