import React from "react";

export default class Screen extends React.Component {

  render() {
    let contenedor;
    if(this.props.clockState === 'hora') {
      contenedor = <h1>{this.props.clockState}</h1>
    }else if(this.props.clockState === 'cronometro'){
      contenedor = <h1>{this.props.clockState}</h1>
    }
    return (
      <React.Fragment>
        <div className={this.props.power + " options"}>
          <ul>
            <li>Hora</li>
            <li>Cronometro</li>
          </ul>
        </div>
        <div className={this.props.power + " hora"}>
          {contenedor}
        </div>
      </React.Fragment>
    );
  }
}
