import React from "react";

class Hora extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      segundo: "00",
      minuto: "00",
      hora: "00"
    };
  }
  componentDidMount() {
    this.timerID = setInterval(() => this.tick(), 1000);
    this.props.onShanger();
  }

  tick() {
    this.handleGetHora();
  }

  handleGetHora() {
    let momentoActual = new Date();
    let hora = momentoActual.getHours();
    let minuto = momentoActual.getMinutes();
    let segundo = momentoActual.getSeconds();

    if (hora < 10) {
      this.setState({ hora: `0${hora}` });
    } else {
      this.setState({ hora: `${hora}` });
    }

    if (minuto < 10) {
      this.setState({ minuto: `0${minuto}` });
    } else {
      this.setState({ minuto: `${minuto}` });
    }

    if (segundo < 10) {
      this.setState({ segundo: `0${segundo}` });
    } else {
      this.setState({ segundo: `${segundo}` });
    }
  }

  

  componentWillUnmount() {
    clearInterval(this.timerID);
  }

  render() {
    return `${this.state.hora}:${this.state.minuto}.${this.state.segundo}`;
  }
}

export default Hora;
